<template>
    <v-app id="inspire">
        <v-content>
        <v-container class="fill-height" fluid>
            <v-row align="center" justify="center">
            <v-col cols="12" sm="8" md="8">
                <v-card class="elevation-12">
                <v-window v-model="step">
                    <!-- Login -->
                    <v-window-item :value="1">
                    <v-row>
                        <v-col cols="12" md="8">
                        <v-card-text class="mt-12">
                            <h1
                            class="text-center display-2 teal--text text--darken-4"
                            >ENTRAR</h1>
                            <h4 class="text-center mt-4">Preencha o formulário para entrar</h4>
                            <v-alert
                              v-if="status_login['status']==='error'"
                              type="error"
                            >
                                {{ status_login['msg'] }}
                            </v-alert>
                            <v-form ref="form_login">
                            <v-text-field
                                v-model="form_login.username"
                                :rules="[() => !!form_login.username
                                                || 'Insira seu usuario']"
                                label="Usuario"
                                prepend-icon="mdi-account"
                                type="text"
                                color="teal darken-4"
                            />

                            <v-text-field
                                v-model="form_login.password"
                                :rules="[() => !!form_login.password
                                                || 'Insira sua senha']"
                                id="password"
                                label="Senha"
                                prepend-icon="mdi-lock"
                                type="password"
                                color="teal darken-4"
                            />
                            </v-form>
                            <!-- <h3 class="text-center mt-4">Forgot your password ?</h3> -->
                        </v-card-text>
                        <div class="text-center mt-3 mb-3">
                            <v-btn
                              @click="login()"
                              rounded
                              color="teal darken-4"
                              dark
                            >ENTRAR</v-btn>
                        </div>
                        </v-col>
                        <v-col cols="12" md="4" class="teal darken-4">
                        <v-card-text class="white--text mt-12">
                            <h1 class="text-center display-1">DINIZ FINANÇAS</h1>
                            <h5
                            class="text-center"
                            >Ainda não tem cadastro? Venha fazer parte</h5>
                        </v-card-text>
                        <div class="text-center mb-3">
                            <v-btn rounded outlined dark @click="step++">Fazer Cadastro</v-btn>
                        </div>
                        </v-col>
                    </v-row>
                    </v-window-item>

                    <!-- Cadastro -->
                    <v-window-item :value="2">
                    <v-row class="fill-height">
                        <v-col cols="12" md="4" class="teal darken-4">
                        <v-card-text class="white--text mt-12">
                            <h1 class="text-center display-1">DINIZ FINANÇAS</h1>
                            <h5
                            class="text-center"
                            >Já é cadastrado? clique no botão abaixo</h5>
                        </v-card-text>
                        <div class="text-center">
                            <v-btn rounded outlined dark @click="step--">Fazer Login</v-btn>
                        </div>
                        </v-col>

                        <v-col cols="12" md="8">
                        <v-card-text class="mt-12">
                            <h1 class="text-center display-2 teal--text text--darken-4">CADASTRAR</h1>
                            <h4 class="text-center mt-4">Preencha o formulário de cadastro</h4>
                            <!-- Alerta ERROR -->
                            <v-alert
                              v-if="status_cadastro['status']==='error'"
                              type="error"
                            >
                                {{ status_cadastro['msg'] }}
                            </v-alert>
                            <!-- Alerta OK -->
                            <v-alert
                              v-if="status_cadastro['status']==='ok'"
                              type="success"
                            >
                                {{ status_cadastro['msg'] }}
                            </v-alert>
                            <v-form ref="form_cadastro">
                            <v-text-field
                                v-model="form_cadastro.usuario"
                                :rules="[() => !!form_cadastro.usuario
                                                || 'O usuario é obrigatório']"
                                label="Usuario"
                                prepend-icon="mdi-account"
                                type="text"
                                color="teal darken-4"
                            />
                            <v-text-field
                                v-model="form_cadastro.email"
                                :rules="[() => !!form_cadastro.email
                                                || 'O e-mail é obrigatório']"
                                label="E-mail"
                                prepend-icon="mdi-email"
                                type="email"
                                color="teal darken-4"
                            />

                            <v-text-field
                                v-model="form_cadastro.senha1"
                                :rules="[() => !!form_cadastro.senha1
                                                || 'A senha é obrigatória']"
                                id="password1"
                                label="Senha"
                                prepend-icon="mdi-lock"
                                type="password"
                                color="teal darken-4"
                            />

                            <v-text-field
                                v-model="form_cadastro.senha2"
                                :rules="[() => !!form_cadastro.senha2
                                                || 'A senha é obrigatória',
                                        () => form_cadastro.senha1==form_cadastro.senha2
                                                || 'As senhas devem ser iguais']"
                                id="password2"
                                label="Senha"
                                prepend-icon="mdi-lock"
                                type="password"
                                color="teal darken-4"
                            />
                            </v-form>
                        </v-card-text>
                        <div class="text-center mt-n5 mb-3">
                            <v-btn
                              @click="cadastro()"
                              rounded
                              color="teal darken-4"
                              dark
                            >CADASTRAR</v-btn>
                        </div>
                        </v-col>
                    </v-row>
                    </v-window-item>
                </v-window>
                </v-card>
            </v-col>
            </v-row>
        </v-container>
        </v-content>
    </v-app>
</template>
  
<script>
import { mapActions, mapGetters } from 'vuex';
import verifica_autenticado from '@/utils/verifica_autenticado'

export default {
    name: 'AuthView',

    data: () => ({
        step: 1,
        form_login: {},
        form_cadastro: {},
        status_login: {'status': 'null'},
        status_cadastro: {'status': 'null'}
    }),

    props: {
        source: String
    },

    methods: {
        ...mapActions([
            'AuthLoginAction',
            'AuthCadastroAction'
        ]),

        ...mapGetters([
            'getResponseCadastro'
        ]),

        async login() {
            if (this.$refs.form_login.validate()) {
                await this.AuthLoginAction(this.form_login)

                if (verifica_autenticado()) {
                    this.$router.push('dividas')
                }  else {
                    this.status_login = {
                        'status': 'error',
                        'msg': 'Usuario ou senha incorretos'
                    }
                }
            }
        },

        async cadastro() {
            if (this.$refs.form_cadastro.validate()) {
                await this.AuthCadastroAction(this.form_cadastro)
                this.status_cadastro = this.getResponseCadastro()
            }
        },
    }
};
</script>
