<template>
  <v-app>
    <HeaderComp v-if="verifica_auth()" />

    <v-main>
      <router-view/>
    </v-main>

    <FooterComp v-if="verifica_auth()" />
  </v-app>
</template>

<script>
import HeaderComp from '@/components/HeaderComp'
import FooterComp from '@/components/FooterComp'
import verifica_autenticado from '@/utils/verifica_autenticado'

export default {
  name: 'App',

  components: {
    HeaderComp,
    FooterComp
  },

  methods: {
    verifica_auth() {
      if(this.$route.path !== '/'){
        if(verifica_autenticado()) {
          return true;
        } else {
          this.$router.push('/')
        }
      } else {
        return false
      }
    }
  }
};
</script>

<style>
.v-main {
  background-color: rgb(47, 45, 45);
}
</style>
